<template>
  <div class="yeardetial">
    <sHeader :name= "title"  />
    <van-collapse v-for="(item, index) in yearList" :key="index" v-model="item.active" accordion>
      <van-collapse-item  :title="item['date']" :name="1" >
        <div class="list_record"  >
          <div> <span>运动总时长</span> <span>{{ item['sportTimeSum']}}</span> </div>
          <div> <span>热量总消耗</span> <span>{{ item['heatSum']}}千卡</span> </div>
          <div> <span>总步数</span> <span>{{ item['stepNumberSum']}}步</span> </div>
          <div> <span>总距离</span> <span>{{ item['distanceSum']}}米</span> </div>
          <div class="list_record" v-for="(childItem, childIndex) in item['list']" :key="childIndex">
            <div> <span>{{ getSportName(childItem['sportType'])}}</span> <span>{{ getSportTime(childItem['sportTime'])}}</span> </div>
          </div>
        </div>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import {onMounted, reactive, toRefs} from "vue";
import sHeader from '@/components/SimpleHeader'
import { useRoute } from "vue-router";
import {formatMMTOHHMM} from "@/utils/util";
import {useStore} from "vuex";

export default {
  name: "YearDetialList",
  components:{
    sHeader
  },
  setup(){
    const router = useRoute()
    const store = useStore()
    /**
     * 声明
     * @type {UnwrapNestedRefs<{list: []}>} 数据列表
     */
    const state = reactive({
      yearList:[],
      params: {},
      title:""
    })

    onMounted(()=>{
      const { query: { itemJson } } = router
      const item = JSON.parse(itemJson)

      // state.params = query.item
      state.title = item.yearDate
      state.yearList = item.origin
    })
    const getSportTime = (time) => {
      return formatMMTOHHMM(time)
    }

    const getSportName = (value) => {
      console.log("sportTypeObj",value)
      console.log("sportTypeObj",store.state.sportTypeObj[`${ Number(value) }`])
      return store.state.sportTypeObj[`${ value }`]
    }
    const initDate = () =>{
      const mlist = []
      for (let i = 0; i < 5; i++) {
        mlist.push({
          date: `2021-8-${Number(13+i)}`,
          zaoCan: 100,
          wuCan: 400,
          wanCan: 500,
          jiaCan: i+100,
          sum: 1000,
          value: 120+ i*2
        })
      }
      return mlist
    }
    return {
      getSportName,
      getSportTime,
      ...toRefs(state)
    }
  }
}
</script>

